"use client";
import { useForm, SubmitHandler } from "react-hook-form";
import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import Navbar from "@/components/Navbar";

interface FormValues {
  name: string;
  email: string;
  phone: string;
  address: string;
  packageType: "basic" | "premium";
  deliveryFrequency: "monthly" | "bimonthly" | "quarterly";
  customizations?: string[];
  specialRequests?: string;
}

export default function SubscriptionPage() {
  const searchParams = useSearchParams();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitMessage, setSubmitMessage] = useState("");
  const [selectedPackage, setSelectedPackage] = useState<"basic" | "premium">("basic");

  const {
    register,
    handleSubmit,
    watch,
    setValue,
    formState: { errors },
  } = useForm<FormValues>();

  const watchedPackageType = watch("packageType");

  useEffect(() => {
    const packageParam = searchParams.get("package");
    if (packageParam === "basic" || packageParam === "premium") {
      setSelectedPackage(packageParam);
      setValue("packageType", packageParam);
    }
  }, [searchParams, setValue]);

  const onSubmit: SubmitHandler<FormValues> = async (data) => {
    setIsSubmitting(true);
    setSubmitMessage("");
    
    try {
      const res = await fetch("/api/subscribe", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(data),
      });
      
      const result = await res.json();
      
      if (!res.ok) {
        throw new Error(result.message || "Subscription failed");
      }
      
      setSubmitMessage("üéâ Subscription successful! Welcome to the Peer Care family. You'll receive a confirmation email shortly.");
    } catch (error: any) {
      setSubmitMessage(`‚ùå Error: ${error.message}`);
    } finally {
      setIsSubmitting(false);
    }
  };

  const premiumCustomizations = [
    "Premium skincare products",
    "Aromatherapy oils & candles",
    "Gourmet comfort foods",
    "Wellness supplements",
    "Luxury bath products",
    "Mindfulness & meditation items",
    "Extra heating pads",
    "Organic cotton products only"
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-pink-50 to-purple-50">
      <Navbar />
      
      <main className="px-4 py-16 max-w-4xl mx-auto">
        {/* Header Section */}
        <div className="text-center mb-12">
          <h1 className="text-5xl md:text-6xl font-bold mb-6 text-gray-900">
            Subscribe to <span className="text-pink-600">Care</span>
          </h1>
          <p className="text-xl text-gray-700 max-w-3xl mx-auto leading-relaxed">
            Join thousands of people who trust us to deliver comfort and care right to their doorstep. 
            Your journey to better self-care starts here.
          </p>
        </div>

        {/* Form Section */}
        <div className="bg-white rounded-3xl p-8 md:p-12 shadow-xl">
          {submitMessage && (
            <div className={`mb-8 p-4 rounded-xl ${
              submitMessage.includes("successful") 
                ? "bg-green-50 text-green-800 border border-green-200" 
                : "bg-red-50 text-red-800 border border-red-200"
            }`}>
              {submitMessage}
            </div>
          )}

          <form onSubmit={handleSubmit(onSubmit)} className="space-y-8">
            {/* Personal Information */}
            <section>
              <h2 className="text-2xl font-bold mb-6 text-gray-900">Personal Information</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-2">
                    Full Name *
                  </label>
                  <input
                    className="w-full px-4 py-3 border-2 border-gray-200 rounded-xl focus:border-pink-500 focus:outline-none transition-colors"
                    placeholder="Enter your full name"
                    {...register("name", { required: "Name is required" })}
                  />
                  {errors.name && (
                    <p className="text-red-500 text-sm mt-1">{errors.name.message}</p>
                  )}
                </div>

                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-2">
                    Email Address *
                  </label>
                  <input
                    className="w-full px-4 py-3 border-2 border-gray-200 rounded-xl focus:border-pink-500 focus:outline-none transition-colors"
                    type="email"
                    placeholder="Enter your email"
                    {...register("email", { 
                      required: "Email is required",
                      pattern: {
                        value: /^\S+@\S+$/i,
                        message: "Please enter a valid email address"
                      }
                    })}
                  />
                  {errors.email && (
                    <p className="text-red-500 text-sm mt-1">{errors.email.message}</p>
                  )}
                </div>

                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-2">
                    Phone Number *
                  </label>
                  <input
                    className="w-full px-4 py-3 border-2 border-gray-200 rounded-xl focus:border-pink-500 focus:outline-none transition-colors"
                    type="tel"
                    placeholder="Enter your phone number"
                    {...register("phone", { required: "Phone number is required" })}
                  />
                  {errors.phone && (
                    <p className="text-red-500 text-sm mt-1">{errors.phone.message}</p>
                  )}
                </div>

                <div>
                  <label className="block text-lg font-medium text-gray-700 mb-2">
                    Delivery Address *
                  </label>
                  <input
                    className="w-full px-4 py-3 border-2 border-gray-200 rounded-xl focus:border-pink-500 focus:outline-none transition-colors"
                    placeholder="Enter your delivery address"
                    {...register("address", { required: "Address is required" })}
                  />
                  {errors.address && (
                    <p className="text-red-500 text-sm mt-1">{errors.address.message}</p>
                  )}
                </div>
              </div>
            </section>

            {/* Package Selection */}
            <section>
              <h2 className="text-2xl font-bold mb-6 text-gray-900">Choose Your Package</h2>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                <label className={`cursor-pointer p-6 border-2 rounded-xl transition-all ${
                  watchedPackageType === "basic" 
                    ? "border-pink-500 bg-pink-50" 
                    : "border-gray-200 hover:border-pink-300"
                }`}>
                  <input
                    type="radio"
                    value="basic"
                    className="sr-only"
                    {...register("packageType", { required: "Please select a package" })}
                  />
                  <div className="text-center">
                    <div className="text-3xl mb-3">üì¶</div>
                    <h3 className="text-xl font-semibold mb-2">Basic Package</h3>
                    <p className="text-gray-600 mb-3">Essential care items</p>
                    <p className="text-2xl font-bold text-pink-600">$29.99/month</p>
                  </div>
                </label>

                <label className={`cursor-pointer p-6 border-2 rounded-xl transition-all ${
                  watchedPackageType === "premium" 
                    ? "border-purple-500 bg-purple-50" 
                    : "border-gray-200 hover:border-purple-300"
                }`}>
                  <input
                    type="radio"
                    value="premium"
                    className="sr-only"
                    {...register("packageType", { required: "Please select a package" })}
                  />
                  <div className="text-center">
                    <div className="text-3xl mb-3">‚ú®</div>
                    <h3 className="text-xl font-semibold mb-2">Premium Package</h3>
                    <p className="text-gray-600 mb-3">Fully customizable</p>
                    <p className="text-2xl font-bold text-purple-600">$49.99/month</p>
                  </div>
                </label>
              </div>
              {errors.packageType && (
                <p className="text-red-500 text-sm mt-2">{errors.packageType.message}</p>
              )}
            </section>

            {/* Delivery Frequency */}
            <section>
              <h2 className="text-2xl font-bold mb-6 text-gray-900">Delivery Frequency</h2>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {[
                  { value: "monthly", label: "Monthly", desc: "Every month" },
                  { value: "bimonthly", label: "Bi-Monthly", desc: "Every 2 months" },
                  { value: "quarterly", label: "Quarterly", desc: "Every 3 months" }
                ].map((option) => (
                  <label key={option.value} className="cursor-pointer p-4 border-2 border-gray-200 rounded-xl hover:border-pink-300 transition-colors">
                    <input
                      type="radio"
                      value={option.value}
                      className="sr-only"
                      {...register("deliveryFrequency", { required: "Please select delivery frequency" })}
                    />
                    <div className="text-center">
                      <h4 className="font-semibold text-gray-900">{option.label}</h4>
                      <p className="text-sm text-gray-600">{option.desc}</p>
                    </div>
                  </label>
                ))}
              </div>
              {errors.deliveryFrequency && (
                <p className="text-red-500 text-sm mt-2">{errors.deliveryFrequency.message}</p>
              )}
            </section>

            {/* Premium Customizations */}
            {watchedPackageType === "premium" && (
              <section>
                <h2 className="text-2xl font-bold mb-6 text-gray-900">Customize Your Premium Package</h2>
                <p className="text-gray-600 mb-6">Select additional items you'd like to include in your package:</p>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {premiumCustomizations.map((item, index) => (
                    <label key={index} className="flex items-center p-4 border border-gray-200 rounded-xl hover:bg-gray-50 cursor-pointer">
                      <input
                        type="checkbox"
                        value={item}
                        className="mr-3 w-4 h-4 text-purple-600 border-gray-300 rounded focus:ring-purple-500"
                        {...register("customizations")}
                      />
                      <span className="text-gray-700">{item}</span>
                    </label>
                  ))}
                </div>
              </section>
            )}

            {/* Special Requests */}
            <section>
              <h2 className="text-2xl font-bold mb-6 text-gray-900">Special Requests (Optional)</h2>
              <textarea
                className="w-full px-4 py-3 border-2 border-gray-200 rounded-xl focus:border-pink-500 focus:outline-none transition-colors"
                rows={4}
                placeholder="Any special requests, allergies, or preferences we should know about?"
                {...register("specialRequests")}
              />
            </section>

            {/* Submit Button */}
            <div className="text-center">
              <button
                type="submit"
                className="px-12 py-4 bg-gradient-to-r from-pink-600 to-purple-600 text-white rounded-full hover:from-pink-700 hover:to-purple-700 transition-all duration-300 font-semibold text-lg shadow-lg hover:shadow-xl transform hover:-translate-y-1 disabled:opacity-50 disabled:cursor-not-allowed"
                disabled={isSubmitting}
              >
                {isSubmitting ? "Processing..." : "Start My Subscription"}
              </button>
              <p className="text-sm text-gray-600 mt-4">
                By subscribing, you agree to our terms of service and privacy policy. 
                You can cancel or modify your subscription anytime.
              </p>
            </div>
          </form>
        </div>
      </main>
    </div>
  );
}
